.btn { background-color: transparent; font-size: 14px; user-select: none; cursor: pointer; }
.btn_border { position: relative; padding: 10px; border: 1px solid #9a9a9a; color: #9a9a9a; }
.btn_border:after { position: absolute; transition: all 0.3s ease; content: ""; width: 0; bottom: 0; left: inherit; right: 0; height: 3px; background: transparent; }
.btn_border:hover { color: #9a9a9a; border-color: transparent; }
.btn_border:hover:after { transition: all 0.3s ease; width: 100%; left: 0; background: #9a9a9a; }
.btn_red { background-color: #f6898b; color: white; }
.btn_small { padding: 12px; font-size: 12px; text-transform: uppercase; }
.btn_bg-yellow, .btn_bg-red, .btn_bg-blue { position: relative; color: #fff; }
.btn_bg-yellow:after, .btn_bg-red:after, .btn_bg-blue:after { position: absolute; transition: all 0.3s ease; content: ""; width: 0; bottom: 0; left: inherit; right: 0; height: 3px; background: transparent; }
.btn_bg-yellow { background-color: #fbdc88; }
.btn_bg-yellow:hover { color: #fbdc88; }
.btn_bg-yellow:hover:after { transition: all 0.3s ease; width: 100%; left: 0; background: #fbdc88; }
.btn_bg-blue { background-color: #a3d7d1; }
.btn_bg-blue:hover { color: #a3d7d1; }
.btn_bg-blue:hover:after { transition: all 0.3s ease; width: 100%; left: 0; background: #a3d7d1; }
.btn_bg-red { background-color: #f6898b; }
.btn_bg-red:hover { color: #f6898b; }
.btn_bg-red:hover:after { transition: all 0.3s ease; width: 100%; left: 0; background: #f6898b; }
.btn_hover { transition: all 0.7s ease; }
.btn_hover:hover { background: transparent; }

